import { CardsProvider, ProviderType, getProvider } from "../cards.provider";
import { ROLE_DEV, ROLE_MNG, ROLE_PO, ROLE_QA } from "./index";

export const getActions = (role: string): string[] => {
    switch (role) {
        case ROLE_DEV: return [
            'Ignore',
            'Assign to me',
            'Follow',
        ]
        case ROLE_QA: return [
            'Reopen',
            'Tested',
            'Ignore',
        ]
        case ROLE_MNG: return [
            'Fire',
            'Promote',
            'Ignore',
            'Schedule 1:1'
        ]
        case ROLE_PO: return [
            'Unassign from any PI',
            'Move to next PI',
            'Ignore',
            'Assign to current iteration'
        ]
    }
    return []
}
