import { useMemo } from "react";
import "./TinderCard.css";
import TinderCard from "react-tinder-card";
import { DraggableCard } from "./cards/DraggableCard";
import { getCurrentUserRole, ROLE_MNG } from "./cards.actions";
import { getProvider, ProviderType } from "./cards.provider";

export let counterTinder = 0;

const getProviderByRole = () => {
  return getProvider(getCurrentUserRole() === ROLE_MNG ? ProviderType.user : ProviderType.assignable);
};

function TinderCards() {
  const role = getCurrentUserRole();
  const provider = getProviderByRole();

  const data = useMemo(() => provider.getCards(), [role]);
  // piece of code which run based on condition

  console.log(data);
  return (
    <div>
      <div className="tinderCards__cardContainer">
        {data.map((info) => (
          <TinderCard
            onSwipe={() => {
              ++counterTinder;
            }}
            className="swipe"
            key={info.name}
            preventSwipe={["up", "down"]}
          >
            <DraggableCard info={info} />
          </TinderCard>
        ))}
      </div>
    </div>
  );
}

export default TinderCards;
