import { isString } from "util"

function addHeader(init: RequestInit, key: string, value: string) {
  if (!init.headers) {
    init.headers = {
      [key]: value
    }
  } else if ((<any>init.headers).append) {
    const headers: any = init.headers
    headers.append(key, value)
  } else {
    (init as any).headers[key] = value
  }
}

function enrichUrlWithUserTokenIfNeeded(url: string, userToken?: string): string {
  if (userToken) {
    url += url.indexOf('?') < 0 ? '?' : '&'
    url += 'access_token='
    url += userToken
  }
  return url
}

const getRequestUrl = (request: RequestInfo) => {
  return isString(request) ? request : (request as Request).url
}


export const tpHttpFetch = (
  baseHttp: {
    fetch(url: RequestInfo, init?: RequestInit): Promise<Response>
  },
  userAccessToken?: string
) => ({
  fetch: async (input: RequestInfo, initParam?: RequestInit) => {
    const init = initParam ?? {}
    const requestUrl = enrichUrlWithUserTokenIfNeeded(getRequestUrl(input), userAccessToken)


    const response = await baseHttp.fetch(requestUrl, init)
    return response
  }
})
