// use extension to fix CORS https://medium.com/@dtkatz/3-ways-to-fix-the-cors-error-and-how-access-control-allow-origin-works-d97d55946d9
import {tpHttpFetch} from "../clients/fetch";
import fetch from "unfetch";
import {Client} from "../clients/tp";

export const getClient = () => {
    const fetchTp = tpHttpFetch({fetch: fetch as any}, 'MTo3VERNNlFXQU5IZm1EU3g4UmZYSU96OUpuM3hZOGt0NnpqVjA3QmpkREQ4PQ==')
    return new Client('http://tplocal.com', fetchTp as any)
}

export const ROLE_DEV = 'Developer'
export const ROLE_QA = 'QA'
export const ROLE_PO = 'Product Owner'
export const ROLE_MNG = 'Manager'
export const roles = [ROLE_DEV, ROLE_QA, ROLE_PO, ROLE_MNG]

export const getCurrentUserRole = () => {
    return localStorage.getItem('role') ?? roles[0]
}