import React, {useEffect, useState} from 'react';
import {Container, Grid, MenuItem, Select} from "@material-ui/core";
import {getCurrentUserRole} from "./cards.actions";
import {getActions} from "./cards.actions/actions.presets";

function ActionSettings() {
    const role = getCurrentUserRole()
    const actions = getActions(role)
    const [currentPosAction, setCurrentPosAction] = useState(localStorage.getItem('positiveAction'))
    const [currentNegAction, setCurrentNegAction] = useState(localStorage.getItem('negativeAction'))

    useEffect(() => {
        if (currentPosAction && !actions.includes(currentPosAction)) {
            localStorage.setItem('positiveAction', actions[0])
            setCurrentPosAction(actions[0])
        }
        if (currentNegAction && !actions.includes(currentNegAction)) {
            localStorage.setItem('negativeAction', actions[1])
            setCurrentPosAction(actions[0])
        }
    }, [])

    return (
        <Container className="chats">
            <Grid style={{
                marginTop: '8px',
                marginBottom: '8px',
            }} container justify={'center'} alignContent={'center'}>
                <Grid item xs={6}>
                    Swipe right action:
                </Grid>
                <Grid item xs={6}>
                    <Select
                        fullWidth
                        value={currentPosAction ?? actions[0]}
                        onChange={(e) => {
                            localStorage.setItem('positiveAction', e.target.value as string)
                            setCurrentPosAction(e.target.value as string)
                        }}>
                        {actions.map(a => <MenuItem value={a}>{a}</MenuItem>)}
                    </Select>
                </Grid>
            </Grid>
            <Grid container style={{
                marginTop: '8px',
                marginBottom: '8px',
            }} >
                <Grid item xs={6}>
                    Swipe left action:
                </Grid>
                <Grid item xs={6}>
                    <Select
                        fullWidth
                        value={currentNegAction ?? actions[0]}
                        onChange={(e) => {
                            localStorage.setItem('negativeAction', e.target.value as string)
                            setCurrentNegAction(e.target.value as string)
                        }}>
                        {actions.map(a => <MenuItem value={a}>{a}</MenuItem>)}
                    </Select>
                </Grid>
            </Grid>
        </Container>
    )
}

export default ActionSettings
