import { Home } from "@material-ui/icons";
import { UserInfo } from "../cards.provider/user";
import "./Cards.css";

type UserCardProps = {
  info: UserInfo;
};
export const UserCard = (props: UserCardProps) => {
  const info = props.info;
  return (
    <div className="user-card">
      <div className="card-header">
        <h3>{info.name}</h3>
      </div>
      <div className="avatar">
        <img src={info.avatar} />
      </div>
      <div className="home">
        <Home /> <div className="home__text">{info.location}</div>
      </div>
      <div>
        <strong>Age:</strong> {info.age}
      </div>
    </div>
  );
};
