import { useState, useRef } from "react";
import { getActions } from "../cards.actions/actions.presets";
import { getCurrentUserRole } from "../cards.actions";
import { CardInfo } from "../cards.provider";
import { AssignableCard } from "./AssignableCard";
import { AssignableInfo } from "../cards.provider/assignable";
import { UserCard } from "./UserCard";
import { UserInfo } from "../cards.provider/user";

interface CardProps {
  info: CardInfo;
}

interface ActionProp {
  direction: "left" | "right";
}

const Action = (prop: ActionProp) => {
  const actions = getActions(getCurrentUserRole());
  const leftAction = localStorage.getItem('positiveAction') ?? actions[0]
  const rightAction = localStorage.getItem('negativeAction') ?? actions[1]
  const text = prop.direction === "left" ? leftAction : rightAction;
  return <div className={`action ${prop.direction}`}>{text}</div>;
};

// id
// name
// role
// location
// age
// avatar
export const DraggableCard = (props: CardProps) => {
  const { info } = props;
  const [isPressed, setIsPressed] = useState(false);
  const start = useRef(0);
  const [direction, setDirection] = useState<string | null>(null);

  return (
    <div
      className="card"
      onTouchStart={(e) => {
        start.current = e.touches[0].clientX;
        setIsPressed(true);
      }}
      onMouseDown={(e) => {
        start.current = e.clientX;
        setIsPressed(true);
      }}
      onMouseUp={() => {
        setIsPressed(false);
      }}
      onTouchMove={(e) => {
        const diff = e.touches[0].clientX - start.current;
        if (diff === 0) {
          return;
        }
        const newDir = diff > 0 ? "left" : "right";
        if (direction !== newDir) {
          setDirection(newDir);
        }
      }}
      onMouseMove={(e) => {
        const diff = e.clientX - start.current;
        if (diff === 0) {
          return;
        }
        const newDir = diff > 0 ? "left" : "right";
        if (direction !== newDir) {
          setDirection(newDir);
        }
      }}
      onTouchEnd={() => {
        setIsPressed(false);
      }}
    >
      {getCurrentUserRole() === "Manager" && <UserCard info={info as UserInfo} />}
      {isPressed && direction && <Action direction={direction as any} />}
      {getCurrentUserRole() !== "Manager" && <AssignableCard info={info as AssignableInfo} />}
    </div>
  );
};
