import { Home, LocationOn } from "@material-ui/icons";
import { AssignableInfo } from "../cards.provider/assignable";
import { SingleTermLabel } from "@targetprocess/entity-label";

type AssignableCardProps = {
  info: AssignableInfo;
};

export const AssignableCard = (props: AssignableCardProps) => {
  const info = props.info;
  const thisIsMyCopy = info.description;
  return (
    <div>
      <div className="card-header">
        <div className="e-label">
          <SingleTermLabel entityType={info.type} text={info.type.toUpperCase()} />
        </div>
        <h3>{info.name}</h3>
      </div>
      <div className=""></div>
      <div className="cut-text" dangerouslySetInnerHTML={{ __html: thisIsMyCopy }}></div>
      <div style={{ alignSelf: "flex-end", marginTop: "48px" }}>
        <div style={{ margin: 4, display: "flex", flexGrow: 1, alignItems: "center" }}>
          <div className="assignee-container">
            <img src={info.assigneePic} />
          </div>
          <span style={{ marginLeft: 8, fontWeight: "bold", flexGrow: 1 }}>{info.asignee}</span>
          <div style={{ fontSize: "small" }}>Effort: {info.effort}</div>
        </div>
        <div style={{ marginTop: 4, marginBottom: 4, display: "flex", flexGrow: 1, alignItems: "center" }}>
          <Home style={{ marginRight: 4 }} /> {info.iterationName}
        </div>
        <div style={{ marginTop: 4, marginBottom: 4, display: "flex", flexGrow: 1, alignItems: "center" }}>
          <LocationOn style={{ marginRight: 4 }} /> Lead time: {info.leadTime}
        </div>
      </div>
    </div>
  );
};
