import {
  Button,
  Card,
  CardActionArea,
  CardActions,
  CardHeader,
  Container,
  IconButton,
  Paper,
  Typography,
} from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { Add, AddBox, AddOutlined, Delete } from "@material-ui/icons";
import { randomIntFromInterval } from "./cards.provider/assignable";
import { useEffect, useState } from "react";

const baseFilters = [
  {
    id: 1,
    title: "Next PI",
    items: 169,
  },
  {
    id: 2,
    title: "Current PI",
    items: 420,
  },
  {
    id: 3,
    title: "Current iteration",
    items: 2137,
  },
];

export const YourFilters = () => {
  const [filters, setFilters] = useState(baseFilters);
  const history = useHistory();

  useEffect(() => {
    const additionalItems = JSON.parse(localStorage.getItem("customFilterNames") ?? "[]") as string[];
    const newFilters = [...filters];
    for (const item of additionalItems) {
      newFilters.push({
        id: randomIntFromInterval(100, 500),
        items: randomIntFromInterval(1, 500),
        title: item,
      });
    }
    setFilters(newFilters);
  }, []);

  return (
    <Container>
      <Typography variant={"h5"} align={"center"} style={{ marginBottom: "16px" }}>
        Your filters
      </Typography>
      {filters.map((filter) => (
        <Card elevation={8} style={{ marginBottom: 8 }} key={filter.title}>
          <CardActionArea onClick={() => history.push(`/filter/${filter.id}`)}>
            <CardHeader
              title={filter.title}
              subheader={`${filter.items} items`}
              action={
                <IconButton
                  onMouseDown={(event) => event.stopPropagation()}
                  style={{ color: "red" }}
                  onClick={(event) => {
                    event.stopPropagation();
                    event.preventDefault();
                    const newArray = filters.filter((f) => f !== filter);
                    const newLocalStorage = (
                      JSON.parse(localStorage.getItem("customFilterNames") ?? "[]") as string[]
                    ).filter((elem) => elem !== filter.title);
                    localStorage.setItem("customFilterNames", JSON.stringify(newLocalStorage));
                    setFilters(newArray);
                  }}
                >
                  <Delete />
                </IconButton>
              }
            />
          </CardActionArea>
        </Card>
      ))}
      <Button startIcon={<AddBox />} onClick={() => history.push("/create-filter")}>
        Add new filter
      </Button>
    </Container>
  );
};
