import { Route, HashRouter, Switch, Redirect } from "react-router-dom";
import "./App.css";
import ActionSettings from "./ActionSettings";
import Header from "./Header";
import SwipeButtons from "./SwipeButtons";
import TinderCards from "./TinderCards";
import { YourFilters } from "./YourFilters";
import { CreateFilter } from "./CreateFilter";

function App() {
  return (
    <div className="app">
      <HashRouter>
        <Route path="/action-settings" exact>
          <Header backButton="/" />
          <ActionSettings />
        </Route>
        <Route path="/create-filter" exact>
          <Header backButton="/" />
          <CreateFilter />
        </Route>
        <Route path="/filter/:filterId" exact>
          <Header backButton="/" />
          <TinderCards />
          <SwipeButtons />
        </Route>
        <Route path="/" exact>
          <Header />
          <YourFilters />
        </Route>
      </HashRouter>

      {/* Header */}

      {/* Tinder cards */}
      {/* Buttons below tinder cards */}

      {/* Chats Screen */}
      {/* Individual chat screen */}
    </div>
  );
}

export default App;
