import {Button, Container, InputLabel, MenuItem, Select, Slider, TextField, Typography} from "@material-ui/core";
import {useState} from "react";
import {useHistory} from "react-router-dom";

const entityTypes = ['UserStory', 'Bug', 'Feature', 'Epic', 'PortfolioEpic']
const statuses = ['New', 'Planned', 'In Dev', 'Coded', 'Ready for testing', 'Testing', 'Tested', 'Merged', 'Validation', 'Done']
const teams = ['Inteam', 'Lambda', 'Svekla']
const iterations = ['PI12 Iteration 1', 'PI12 Iteration 2', 'PI12 Iteration 3', 'PI13 Iteration 1', 'PI13 Iteration 2', 'PI13 Iteration 3']

export const CreateFilter = () => {
    const history = useHistory()
    const [filterName, setFilterName] = useState('')
    const [selectedType, setSelectedType] = useState([entityTypes[0]])
    const [selectedStatus, setSelectedStatus] = useState([statuses[0]])
    const [selectedTeam, setSelectedTeam] = useState([teams[0]])
    const [selectedIteration, setSelectedIteration] = useState([iterations[0]])
    const [estimation, setEstimation] = useState<number[]>([3, 8]);
    const [age, setAge] = useState<number[]>([21, 37]);

    const handleEstimationChange = (event: any, newValue: number | number[]) => {
        setEstimation(newValue as number[]);
    };

    const handleAgeChange = (event: any, newValue: number | number[]) => {
        setAge(newValue as number[]);
    };

    // @ts-ignore
    return (
        <Container>
            <Typography variant={'h5'} align={'center'} style={{marginBottom: '16px'}}>
                Discovery settings
            </Typography>
            <TextField value={filterName} onChange={e => setFilterName(e.target.value as string)} style={{marginBottom: 8}} placeholder={'Filter name'} variant={'outlined'} size={'small'} fullWidth />
            <Typography variant={'h6'} align={'left'} style={{marginBottom: '4px'}}>
                Card size
            </Typography>
            <Slider value={estimation}
                    onChange={handleEstimationChange}
                    step={null}
                    style={{marginBottom: 32}}
                    marks={[
                        {value: 1, label: '1'},
                        {value: 2, label: '2'},
                        {value: 3, label: '3'},
                        {value: 5, label: '5'},
                        {value: 8, label: '8'},
                        {value: 13, label: '13'},
                        {value: 21, label: '21'},
                    ]}
                    min={1}
                    max={21}
                    valueLabelDisplay="auto"/>
            Entity type:
            <Select style={{marginBottom: 8}} value={selectedType} onChange={e => setSelectedType(e.target.value as string[])} fullWidth variant='outlined'>
                {entityTypes.map(type => <MenuItem value={type} key={type}>{type}</MenuItem>)}
            </Select>
            Status:
            <Select style={{marginBottom: 8}} value={selectedStatus} onChange={e => setSelectedStatus(e.target.value as string[])} fullWidth variant='outlined'>
                {statuses.map(status => <MenuItem value={status} key={status}>{status}</MenuItem>)}
            </Select>
            Team:
            <Select style={{marginBottom: 8}} value={selectedTeam} onChange={e => setSelectedTeam(e.target.value as string[])} fullWidth variant='outlined'>
                {teams.map(team => <MenuItem value={team} key={team}>{team}</MenuItem>)}
            </Select>
            Iteration:
            <Select style={{marginBottom: 8}} value={selectedIteration} onChange={e => setSelectedIteration(e.target.value as string[])} fullWidth variant='outlined'>
                {iterations.map(iter => <MenuItem value={iter} key={iter}>{iter}</MenuItem>)}
            </Select>
            <Typography variant={'h6'} align={'left'} style={{marginBottom: '4px'}}>
                Card age
            </Typography>
            <Slider value={age}
                    onChange={handleAgeChange}
                    min={0}
                    max={100}
                    valueLabelDisplay="auto"/>
            <Button variant={'contained'} fullWidth onClick={() => {
                history.replace('/')
                const currentFilters = JSON.parse(localStorage.getItem('customFilterNames') ?? '[]') as string[]
                currentFilters.push(filterName)
                localStorage.setItem('customFilterNames', JSON.stringify(currentFilters))
            }}>
                Save
            </Button>
        </Container>
    )
}