import React from 'react';
import './Header.css';
import PersonIcon from '@material-ui/icons/Person';
import TuneIcon from '@material-ui/icons/Tune';
import IconButton from '@material-ui/core/IconButton';
import {Link, useHistory, useLocation} from 'react-router-dom';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import {Grid} from "@material-ui/core";

function Header({ backButton }) {
    // is used to remember the browser history
    const history = useHistory();
    const location = useLocation()

    return (
        <div className = "header">
            <Grid container
                  alignItems="center"
                  justifyContent="end">
                <Grid item xs={3}>
                    {backButton ? (
                        <IconButton size={'small'} onClick = {() => history.replace(backButton)}>
                            <ArrowBackIosIcon fontSize = "large" className = "header__icon" />
                        </IconButton>
                    ) : (
                        <div className = "header__icon" />
                    )}
                </Grid>
                <Grid item xs={6}>
                    <Link to = "/">
                        <img
                            className = "header__logo"
                            src = "https://mma.prnewswire.com/media/82080/apptio_logo.jpg?w=200"
                            alt = "TinderProcess logo"
                        />
                    </Link>
                </Grid>
                <Grid item xs={3} sx={{
                    textAlign: 'end'
                }}>
                    <Link to = "/action-settings">
                        <IconButton disabled={location.pathname === '/action-settings'} size={'small'}>
                            <TuneIcon className = "header__icon" fontSize = "large" />
                        </IconButton>
                    </Link>
                </Grid>
            </Grid>
        </div>
    )
}

export default Header
