import { tpHttpFetch } from "../clients/fetch";
import { Client } from "../clients/tp";
import fetch from "unfetch";
import { AssignableCards } from "./assignable";
import { UserInfos } from "./user";

export enum ProviderType {
  "user",
  "assignable",
}

export interface CardInfo {
  name: string
}

export interface CardsProvider {
  getCards: () => CardInfo[];
}

// use extension to fix CORS https://medium.com/@dtkatz/3-ways-to-fix-the-cors-error-and-how-access-control-allow-origin-works-d97d55946d9
export const getClient = () => {
  const fetchTp = tpHttpFetch(
    { fetch: fetch as any },
    "MTpzQkdRQXV0bmZ4c0k0SXN0WXp2QW9wR0ZkR1hUb1VNbS9TUGw3VldRWkF3PQ=="
  );
  return new Client("https://inteam.tpondemand.net", fetchTp as any);
};

export const getProvider = (type: ProviderType): CardsProvider => {
  const client = getClient();
  const providers = {
    [ProviderType.assignable]: new AssignableCards(client),
    [ProviderType.user]: new UserInfos(client),
  };

  return providers[type] ?? new AssignableCards(client);
};
