import React from 'react';
import './SwipeButtons.css';
import ReplayIcon from '@material-ui/icons/Replay';
import CloseIcon from '@material-ui/icons/Close';
import StarRateIcon from '@material-ui/icons/StarRate';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FlashOnIcon from '@material-ui/icons/FlashOn';
import {IconButton} from '@material-ui/core';
import {getCurrentUserRole, roles} from "./cards.actions";

function setNextRole() {
    const role = getCurrentUserRole()
    const index = roles.indexOf(role)
    const newIndex = (index + 1) % roles.length
    localStorage.setItem('role', roles[newIndex])
}

function SwipeButtons() {
    return (
        <div className="swipeButtons">
            <IconButton onClick={() => console.log('x')} className="swipeButtons__repeat">
                <ReplayIcon fontSize="large"/>
            </IconButton>

            <IconButton onClick={() => console.log('xdd')} className="swipeButtons__left">
                <CloseIcon fontSize="large"/>
            </IconButton>

            <IconButton onClick={setNextRole} className="swipeButtons__star">
                <StarRateIcon fontSize="large"/>
            </IconButton>

            <IconButton className="swipeButtons__right">
                <FavoriteIcon fontSize="large"/>
            </IconButton>

            <IconButton className="swipeButtons__lightning">
                <FlashOnIcon fontSize="large"/>
            </IconButton>
        </div>
    )
}

export default SwipeButtons
